import React from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import mb1 from "../Images/mb1.jpg";
import mb2 from "../Images/mb2.jpg";
import mb3 from "../Images/mb3.jpg";
import mb4 from "../Images/development.jpg";
import "../CSS/MainStyles.css"
import "../CSS/Component.css"
import THeading from "../components/Heading"
import { Link } from "react-router-dom";





const optionss = {
    margin: 30,
    responsiveClass: true,
    // nav: true,
    dots: true,
    autoplay: true,
    // navText: ["Prev", "Next"],
    smartSpeed: 1000,
    responsive: {
        0: {
            items: 1,
        },
        400: {
            items: 1,
        },
        600: {
            items: 2,
        },
        700: {
            items: 3,
        },
        1000: {
            items: 5,

        }
    },
};

const Home = () => {
    
  return (
    <div className='Mhome'>
            <OwlCarousel className='owl-theme m'  
            loop 
            margin={0}
            items={1} 
            dots={false} 
            autoplay={true}
            
            >
    <div className='item'>
        <div className="scrollImg">
            <img src={mb1} alt=""/>
        </div>
    </div>
    <div className='item'>
        <div className="scrollImg">
            <img src={mb2} alt=""/>
        </div>
    </div>
    <div className='item'>
        <div className="scrollImg">
            <img src={mb3} alt=""/>
        </div>
    </div>
</OwlCarousel>

<div className="container ">
{/* part1 */}

     {/* part2 */}
    <div className="col-sm-12">
            <THeading title="Services"/>
                <div className="row mt-3">
                <OwlCarousel className='owl-theme m'  
                
                margin={0}
                items={4} 
                dots={true} 
                autoplay={true}
                {...optionss}>       
                <div className='item'>
                <div className="col-md-12 col-lg-12">
                        <div className="service-item border  p-3  ">
                            <div className="btn-square2 bg-light rounded-circle mb-4">
                                <i className="fas fa-lightbulb" aria-hidden="true"></i>
                            </div>
                            <h4 className="mb-3">Digital Transformation</h4>
                            <p className="mb-4">We have development experience, knowledge, and tech expertise, we are confident that our team can give you game-changing advice.</p>
                            <Link className="btn bg-primary"  > Read More <i className="fa fa-arrow-right text-white ms-3"></i></Link>
                        </div>
                    </div>
                </div>

                    
                    </OwlCarousel>
                </div>


      </div>
      {/* part3 */}
      <div className="col-sm-12">
      <div className="row">
        <div className="col-sm-6">
        <div className="bg-primary mb-3"></div>
      <THeading title="What We Do"/>
      <p class="mb-4 pb-2">Custom software development consulting and application management has evolved. There is no longer a one-size-fits-all approach. You need a custom software solution for your unique business challenges. Weâ€™re here to build what you need. Sun Bright  is a team of developers, architects, and project managers with experience and a singular focus. Using our experience and consideration, we create a custom look and functionality for your software to push your business technology forward in the industry.</p>
        </div>
        <div className="col-sm-6">
            <img src={mb4} className="img-fluid"></img>
        </div>
      </div>
      </div>
</div>
      </div>
  )
}

export default Home